@import "./mixins.scss";

.burn {
    background-image: url('../Assets/image/gif/fluid-color-2.gif');
    background-size: cover;
    background-position: center;
    color: transparent;
    background-clip: text;
    -webkit-background-clip: text;
}

.anm1 {
    animation: anim1 500ms ease-in;
    // animation: anim1 1s ease-in;
    animation-delay: 4s;
}

.anm2 {
    margin-left: -10px;
    animation: anim2 1s ease-in;
    // animation: anim2 2s ease-in;
    animation-delay: 4s;
}

.active {
    background-color: rgb(17 24 39);
    color: white;

    &:hover {
        text-decoration: none;
    }
}


.main-header {
    display: flex;
    justify-content: space-between;
    padding: 0 30px;
    background-color: #333;
    color: #fff;
    align-items: center;
    line-height: 70px;

    .left {
        font-size: 32px;

        // animation: anim1 5s infinite;
        .anm1 {
            animation: anim1 1s ease-in;
            animation-delay: 2s;
        }

        .anm2 {
            margin: 0px 10px;
            animation: anim2 2s ease-in;
            animation-delay: 2s;
        }
    }

    .center {
        display: none;
        gap: 20px;
        list-style: none;
        cursor: pointer;

        li {
            a {
                color: white;
                text-decoration: none;

                &:hover {
                    color: #e4d7d7;
                }
            }
        }


        @include md {
            display: flex;
        }
    }


    .right {
        display: flex;
        gap: 20px;
    }
}

@keyframes anim1 {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

@keyframes anim2 {
    0% {
        margin-left: -10px;
    }

    50% {
        margin-left: 10px;
    }

    100% {
        margin-left: -10px;
    }
}

.cart-products {
    overflow-y: scroll;
    flex-grow: 1;
}

.no-item {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
    margin: 40px 0;
}

.add-to-cart-button {
    outline: 0;
    border: 0;
    height: 50px;
    width: 180px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background: #8e2de2;
    color: white;
    font-size: 16px;
    border-bottom: 3px solid #6516aa;
    flex-grow: 1;

    @include md {
        flex-grow: unset;
    }

    .cart-product {
        padding: 20px 15px;
        display: flex;
        gap: 10px;
    }

    .img-container {
        background-color: rgba(0, 0, 0, 0.5);
        width: 60px;
        height: 60px;
        flex-shrink: 0;

        img {
            width: 100%;
            height: 100%;
        }
    }

    .prod-details {
        overflow: hidden;
        position: relative;

        .name {
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            font-style: 16px;
            line-height: 1;
            margin-bottom: 10px;
            font-weight: 600;
            display: block;
            padding-right: 25px;

        }

        .close-btn {
            font-size: 22px;

            position: absolute;
            top: 0px;
            right: 0px;

        }

        .text {
            display: flex;
            align-items: center;
            gap: 5px;
            font-style: 12px;
            font-weight: 600;

            .highlight {
                color: #8e2de2;

            }
        }

        svg {
            position: absolute;
            top: 0;
            right: 0;
            cursor: pointer;
        }

        .quantity-buttons {
            width: fit-content;
            display: flex;
            border: 1px solid rgba(0, 0, 0, 0.2);
            margin-bottom: 8px;
            height: 30px;

            span {
                font-size: 14px;
                width: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                color: #6b6b6b;

                &:nth-child(1) {
                    font-style: 18px;
                    border-right: 1px solid rgba(0, 0, 0, 0.2);
                }

                &:nth-child(2) {
                    width: 40px;
                }

                &:nth-child(3) {
                    font-style: 18px;
                    border-left: 1px solid rgba(0, 0, 0, 0.2);
                }
            }
        }
    }

    &:hover {
        background-color: rgba(0, 0, 0, 0.05);
    }
}